/**
 * Основные стили
 **/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1140px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.button {
  display: block;
  padding-top: 14px;
  padding-bottom: 14px;
  width: 100%;
  background-color: #FCDB50;
  color: #2f2f2f;
  text-align: center;
  font-size: 0.875rem;
  line-height: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color .5s; }
  .button:hover {
    background-color: #FFE67C; }

.title {
  font-size: 2rem;
  line-height: 2.3125rem;
  color: #FCDB50;
  text-align: center;
  font-weight: 700; }

.subtitle {
  font-size: 1.5rem;
  line-height: 1.75rem;
  text-align: center;
  font-weight: 400; }

.fixed {
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  transform: translateX(-50%);
  z-index: 10;
  transition: background-color .5s; }
  .fixed--active {
    background-color: black; }
  .fixed__wrap {
    display: flex;
    align-items: center; }
  .fixed__hamburger {
    margin-right: 11.3%;
    flex-basis: 24px;
    flex-shrink: 0;
    cursor: pointer; }
    .fixed__hamburger span {
      display: block;
      width: 100%;
      height: 2px;
      margin-bottom: 4px;
      background-color: #FCDB50;
      border-radius: 4px; }
      .fixed__hamburger span:nth-child(3) {
        width: 75%;
        margin-left: auto;
        margin-bottom: 0; }
  .fixed__repair, .fixed__map {
    padding-left: 48px;
    background: url("../img/repair.svg") left no-repeat;
    padding: 24px 0 24px 48px; }
    .fixed__repair p, .fixed__map p {
      font-size: 1.125rem;
      line-height: 1.3125rem; }
    .fixed__repair span, .fixed__map span {
      display: block;
      font-size: 0.875rem;
      line-height: 1rem; }
  .fixed__phone {
    margin-left: auto; }
    .fixed__phone a {
      display: block;
      text-decoration: none; }
    .fixed__phone span {
      display: none;
      color: #fff;
      font-size: 1.5rem;
      line-height: 1.75rem; }
  .fixed__map {
    display: none;
    background-image: url("../img/map.png"); }
  .fixed__button {
    display: none;
    margin-left: 24px;
    max-width: 164px; }
  .fixed__callback {
    display: none;
    font-size: 0.875rem;
    line-height: 1rem;
    margin-left: 24px; }

@media (min-width: 480px) {
  .fixed__hamburger, .fixed__repair {
    margin-right: 5%; }
  .fixed__map {
    display: block; } }

@media (min-width: 680px) {
  .fixed__phone img {
    display: none; }
  .fixed__phone span {
    display: block; } }

@media (min-width: 880px) {
  .fixed__button {
    display: block; } }

@media (min-width: 1024px) {
  .fixed__callback {
    display: block; }
  .fixed__hamburger {
    position: relative; }
    .fixed__hamburger::after {
      position: absolute;
      top: 50%;
      right: -45px;
      content: 'Меню';
      font-size: 0.875rem;
      line-height: 1rem;
      transform: translateY(-50%); } }

.header {
  background: #000 url("../img/screen-bg.jpg") 75% top/cover no-repeat;
  color: #fff;
  position: relative;
  z-index: 1; }
  .header::before {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.3) 0%, rgba(255, 255, 255, 0) 48.96%, rgba(0, 0, 0, 0.3) 100%), rgba(24, 24, 24, 0.8);
    width: 100%;
    height: 100%;
    z-index: -1; }
  .header__wrap {
    padding-top: 1px; }
    .header__wrap-two {
      padding-bottom: 30px; }
  .header__title {
    margin: 125px auto 24px;
    line-height: 44px;
    max-width: 300px; }
  .header__subtitle {
    margin: 24px auto 64px;
    max-width: 300px; }
  .header__button {
    display: block;
    padding-top: 19px;
    padding-bottom: 19px;
    text-decoration: none; }
  .header__leader {
    position: relative;
    display: flex;
    justify-content: space-between;
    max-width: 288px;
    margin: 35px auto 95px;
    background: url("../img/rectangle.png") center/288px 54px no-repeat;
    color: #fff;
    text-decoration: none;
    font-size: 0.875rem;
    line-height: 3.375rem; }
    .header__leader::after {
      position: absolute;
      top: 50%;
      left: 130px;
      display: flex;
      width: 82px;
      height: 83px;
      content: '';
      background: url("../img/medal.png") center no-repeat;
      transform: translateY(-50%); }
    .header__leader span:nth-child(1) {
      margin-left: 30px; }
    .header__leader span:nth-child(2) {
      margin-right: 30px; }

@media (min-resolution: 2dppx) {
  .header {
    background: #000 url("../img/screen-bg-2x.jpg") 75% top/cover no-repeat; }
    .header__leader::after {
      background-image: url("../img/medal-2x.png");
      background-size: cover; } }

@media (min-width: 580px) {
  .header__button {
    flex-basis: 264px;
    margin-right: 34px; }
    .header__buttons {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 78px; }
  .header__leader {
    flex-basis: 288px;
    flex-shrink: 0;
    margin: 0; } }

@media (min-width: 600px) {
  .header {
    background-position: 70% top; } }

@media (min-width: 768px) {
  .header__title {
    font-size: 2.75rem;
    line-height: 3.3125rem;
    max-width: 350px; }
  .header__subtitle {
    font-size: 1.75rem;
    line-height: 2rem;
    max-width: 350px; } }

@media (min-width: 1024px) {
  .header__title, .header__subtitle {
    max-width: none; }
  .header__title {
    font-size: 3.375rem;
    line-height: 3.9375rem;
    padding-top: 203px; }
  .header__subtitle {
    font-size: 2rem;
    line-height: 2.3125rem;
    margin-bottom: 32px; }
  .header__buttons {
    margin-bottom: 90px; } }

.advantages {
  list-style: none; }
  .advantages__item {
    background-color: rgba(0, 0, 0, 0.3);
    margin-bottom: 24px;
    padding: 24px;
    text-align: center;
    border-radius: 5px; }
    .advantages__item:last-child {
      margin-bottom: 0; }
  .advantages__icon {
    max-width: 50px;
    margin: auto; }
    .advantages__icon-wrap {
      width: 50px;
      margin: auto; }
  .advantages__text {
    margin-top: 16px;
    font-size: 1rem;
    line-height: 1.5rem; }

@media (min-width: 1024px) {
  .advantages {
    display: flex;
    max-width: 800px;
    margin: 0 auto 90px; }
    .advantages::after {
      position: absolute;
      left: 0;
      content: '';
      height: 145px;
      width: 95%;
      background: rgba(0, 0, 0, 0.3);
      border-radius: 0 75px 75px 0;
      z-index: -1; }
    .advantages__item {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      flex-basis: 25%;
      padding-left: 0;
      padding-right: 0;
      margin-bottom: 0;
      background: none; }
      .advantages__item:nth-child(2) .advantages__text {
        width: 150px;
        margin: 16px auto 0; }
    .advantages__text {
      font-size: 0.875rem;
      line-height: 1.375rem; } }

@media (min-width: 1250px) {
  .advantages::after {
    width: 85%; } }
